.App {
  /*text-align: center; */
  margin: 0px;
}

.App-logo {
  height: 15vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 15vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

#menu {
  text-align: center;
  background-color: lightslategrey;
  margin-top: 0px;
  padding: 10px;
}
#menu li {
  display: inline;
  margin-right: 15px;
}

.Home, .componentes {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
}
.Home div, .componentes div {
  margin: 5px 15px;
  border: 1px solid gray;
  padding: 10px;
  border-radius: 10px;
}
#HolaMundo, #listado, #estado  {
  width: 450px;
  margin: 0px auto;
}

#HolaMundo {
  text-align: center;
}

.estados * {
 float:left;
 margin: 10px;
}

#peliculas h2 {
    text-align: center;
}

#ListadoPeliculas {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-around;
  border: 0px;
}

.pelicula img {
  
  width: 100%;
  height: 240px;
}
.pelicula {
  width: 160px;
  padding: 5px;
  margin: 2px;
  background-color: rgb(212, 244, 255)
}

.Error{
  font-size: 5vh;
  text-align: center;
}
.Error img {
  width: 600px;
  
}

a:link, a:visited {
  color: white;
  text-decoration: none;
}
a:hover {
  color: red;
  text-decoration: underline;
}
a.activo {
  color: limegreen;
  border-bottom: 1px solid limegreen;
}

.formulario * {
  display: block;
  margin: 0px auto;
  text-align: center;
  margin-bottom:20px;
}

.formulario textarea {
  height: 120px;
}
#usuarios {
  border: 0px;
  padding: 5px;
  display: flex;
  flex-flow: row wrap;
}
.usuario {
  background-color: rgb(230, 230, 146);
}

.usuario {
  width: 250px;
}

.usuario img{
  float: left;
  margin-right: 10px;
}
.usuario p {
  clear: left;
  padding-top: 10px;
}